.jumbotron{
    font-family: "Helvetica", sans-serif;
    letter-spacing: 3px;
    width: 100%;
    text-transform: uppercase;
}
.header{
    font-family: "Helvetica", sans-serif;
    letter-spacing: 3px;
    width: 100%;
    text-transform: uppercase;
    /* background-image: url("../../assets/TEST-LOGO.gif"); */
    background-image: url(/static/media/header.531fd565.jpg);
    
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 20%;
    text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.6);
}

.form-group{
    margin: 25px;
}

.btn .btn-success{
    background-color:black;
    border: none;
}
.card {
    background: #fff;
    border-radius: none;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    border: 0px;
  }
  
  .card > .img-container {
    /* height: 60%; */
    overflow: hidden;
    text-align: center;
    background:white;
  }
  .card > .img-container > img {
    width: 120%
  }
  
  .card > .content {
    /* padding-left: 1rem;
    padding-right: 1rem; */
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .card > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
  .card > .remove:hover {
    color: #d50000;
  }

a {
    color: gray;
    text-decoration: none;
    background-color: transparent;
}

a:hover {
  color: black;
  text-decoration: none;
  background-color: transparent;
}

.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1); 
  cursor: pointer;
}
  
.wrapper {
    background: lightgrey;
    /* height: 100%; */
    display: flex;
    flex-flow: row wrap;
    padding: 100px 20px 100px 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.img-fluid {
