.card {
    background: #fff;
    border-radius: none;
    height: 325px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    border: 0px;
  }
  
  .card > .img-container {
    /* height: 60%; */
    overflow: hidden;
    text-align: center;
    background:white;
  }
  .card > .img-container > img {
    width: 120%
  }
  
  .card > .content {
    /* padding-left: 1rem;
    padding-right: 1rem; */
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
  }
  
  .card > .remove {
    color: #ff1744;
    font-size: 48px;
    position: absolute;
    top: -20px;
    right: 5px;
    cursor: pointer;
  }
  
  .card > .remove:hover {
    color: #d50000;
  }

a {
    color: gray;
    text-decoration: none;
    background-color: transparent;
}

a:hover {
  color: black;
  text-decoration: none;
  background-color: transparent;
}

.card:hover {
  transform: scale(1.1); 
  cursor: pointer;
}
  